/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})



/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 0.1, 100)
const cameraDeviceMotion = new THREE.PerspectiveCamera(45, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 4
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
// const controls = new DeviceOrientationControls(camera, canvas)
controls.enableDamping = true

const orientationControl = new DeviceOrientationControls(cameraDeviceMotion)




// Request

const requestPermission = {
    ShuoZ:()=>{
        DeviceMotionEvent.requestPermission()
    }
}

gui.add(requestPermission, 'ShuoZ')


/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding
renderer.setClearColor('rgb(8,8,32)',1.0)

/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0



const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime
    
    if(mixer !== null){
        mixer.update(deltaTime)
    }


    // Update controls
    controls.update()
    orientationControl.update()


    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)

    // gltf.rotation.x = elapsedTime


    faceLineImg.offset.set(elapsedTime/3,0)
    frontFaceImg.offset.set(Math.cos(elapsedTime*3)/300,Math.sin(elapsedTime*3)/40)




}

tick()

